const downsampleBackgrounds = window.mobileCheck();

const assets = '/assets/frog/assets'; // no trailing slash!
const gfxNames = ['frog-stand', 
                  'water', 
                  'lily', 'lily-down', 'lily-up',
                  'fly', 'bee-left', 'bee-right',
                  'snail', 'crab', 'small-bee-left', 'small-bee-wing-left',
                  'small-bee-right', 'small-bee-wing-right',
                  'fly-bar',
                  'fly-bar-grey',
                  'fly-bubble', 'alert-bubble',
                  'heart',
                  'alt-frog-stand',
                  'bby', 'bby-body', 'bby-frame', 'quest-box',
                  'inventory-button', 'button', 'button-toggled',
                  'sound-off', 'sound-on', 'speaker', 'speaker-off',
                  'bg', 'bg-2', 'bg-3', 'bg-menu', 'bg-palms']
const gfxSheets = ['bby-spritesheet']

const sfxNames = ['may-your-night-be-at-peace.mp3', 'wizard.wav', 'soso.mp3', 'toy-town.mp3', 'the-moon.mp3',
                  'heart.wav', 'hop.wav', 'hop-left.wav', 'tick.wav'];

let gfx = {};
let sfx = {};

/* setup (っ◔◡◔)っ ❤ */

function preload() {
  // images
  for (let obj of gfxNames) {
    const key = camelizeDashes(obj);
    if (downsampleBackgrounds && obj.substring(0, 2) == 'bg') obj += '-mobile';

    gfx[key] = loadImage(`${assets}/img/${obj}.png`);
  }

  for (const obj of gfxSheets) {
      const key = camelizeDashes(obj);
      const img = loadImage(`${assets}/img/${obj}.png`);
      gfx['bbySpritesheet'] = img;
  }

  // font
  pixelFont = loadFont(`${assets}/font/VT323-Regular.ttf`);

  // shaders
  frogShader = loadShader(`${assets}/shader/frog.vert`, 
                          `${assets}/shader/frog.frag`);

  // sound
  for (const obj of sfxNames) {
    const key = camelizeDashes(obj.slice(0, -4));
    sfx[key] = loadSound(`${assets}/sfx/${obj}`);
  }

  sfx.hop.setVolume(.7);
}

// from: https://stackoverflow.com/questions/10787782/full-height-of-a-html-element-div-including-border-padding-and-margin
/** 
 * Returns the element height including margins
 * @param element - element
 * @returns {number}
 */
function outerHeight(element) {
    const height = element.offsetHeight,
        style = window.getComputedStyle(element)

    return ['top', 'bottom']
        .map(side => parseInt(style[`margin-${side}`]))
        .reduce((total, side) => total + side, height)
}
// ...

function calcWindowBounds() {
  //marginX = 0;
  //marginY = 0;
  screenSizeX = 1024;
  screenSizeY = 700;
}

function Begin() {
  this.setup = function() {
    calcWindowBounds();
    let canvas = createCanvas(screenSizeX, screenSizeY);
    canvas.parent('game-container');

    document.getElementById('game-details').style.display = 'block';
  }

  this.draw = function() {
    push();
    stroke(255);
    fill(255);
    textSize(64);
    textAlign(CENTER);
    textFont(pixelFont)
    text('Click or press a key to begin!', screenSizeX / 2, screenSizeY / 2);
    textSize(32);
    stroke(255, 0);
    text('warning: not yet optimized for mobile!', screenSizeX / 2,  5 * screenSizeY / 7);
    pop();
  }

  this.onAction = function() {
    this.sceneManager.showScene(Menu);  
  }

  this.mousePressed = this.onAction;
  this.keyPressed = this.onAction;
}

function setup() {
  document.getElementById('game-container').oncontextmenu = function() { if (mouseX > 0 && mouseX < screenSizeX && mouseY > 0 && mouseY < screenSizeY) return false; }

  /* ~ scene manager ~ */
  let mgr = new SceneManager();

  // inject properties
  mgr.gfx = gfx;
  mgr.sfx = sfx;
  mgr.pixelFont = pixelFont;
  mgr.frogShader = frogShader;

  mgr.prop = {song: sfx.mayYourNightBeAtPeace, musicVol: .7, fxVol: 1.};

  // environment
  generalState.envFunction = linearEnvironment;

  // setup
  mgr.wire();
  //mgr.showScene(FactorsPuzzle, {specialLily: 9, initVals: [2, 3, 4, 5]});
  //mgr.showScene(CyclicFactorsPuzzle, {specialLily: 9, initVals: [2, 3, 4, 5]});
  //mgr.showScene(FrogHeaven);
  mgr.showScene(Begin);
  //mgr.showScene(Menu);
  //mgr.showScene(OpenLinear, {step: 1})
}